import React from 'react'
import { Row, Col } from 'antd'
import Footer from '@/components/app-footer'
import { useBreadcrumb } from '@/hooks/useApp'
import { useUser } from '@/data/useUser'
import useAuth from '@/hooks/useAuth'
import history from '@/utils/history'
import { REPORT_LIST } from '@/constants/routes'
import { Divisions } from '@/constants/enums'
import { Block, HomePage } from './components'

export default function Home() {
  useBreadcrumb([{ name: '首頁' }])
  const setUser = useUser()

  const { user } = useAuth()

  const handleTo = (pathname: string) => {
    if (user.isAdmin) {
      setUser((prev: any) => ({ ...prev, viewDivision: true }))
      return
    }
    history.push(pathname)
  }

  const handleDivisionSelect = (division: Divisions) => {
    setUser((prev: any) => ({
      ...prev,
      division,
      viewDivision: true,
    }))
  }

  const showButtons = user.isClinician || (user.isAdmin && !user.viewDivision)

  const handleClick = (division: Divisions) => () => {
    if (user.isAdmin || user.isClinician) {
      handleDivisionSelect(division)
    }

    if (!user.isAdmin) {
      handleTo(REPORT_LIST)
    }
  }

  return (
    <>
      {!showButtons && <HomePage />}
      {showButtons && (
        <div style={{ height: 'calc(100vh - 204px)' }}>
          <Row>
            <Col span={24} className="text-center">
              <div css={{ marginTop: 200 }}>
                <Block
                  onClick={handleClick(Divisions.RAD)}
                  style={{ marginRight: 100 }}>
                  放射科
                </Block>
                <Block onClick={handleClick(Divisions.PATH)}>病理科</Block>
              </div>
            </Col>
          </Row>
        </div>
      )}
      <Footer />
    </>
  )
}
